/* controls */
.vp-controls,
.vp-controls > * {
  padding: 0;
  margin: 0;
}
.vp-controls {
  overflow: hidden;
  background: transparent;
  width: 100%;
  height: 7%; /* of figure's height */
  position: relative;
}
.vp-controls[data-state="hidden"] {
  display: none;
}
.vp-controls[data-state="visible"] {
  display: block;
}
.vp-controls > * {
  float: left;
  width: 3.30625%;
  height: 100%;
  margin-left: 0.1953125%;
  display: block;
}
.vp-controls > *:first-child {
  margin-left: 0;
}
.vp-controls .vp-progress {
  cursor: pointer;
  width: 75.390625%;
  height: 2rem;
}
.vp-controls button {
  text-align: center;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  border: none;
  cursor: pointer;
  text-indent: -99999px;
  background: transparent;
  background-size: contain;
  background-repeat: no-repeat;
}
.vp-controls button:hover,
.vp-controls button:focus,
.vp-subtitles-menu li button:hover,
.vp-subtitles-menu li button:focus,
.vp-subtitles-menu li button[data-state="active"] {
  opacity: 0.5;
}
.vp-controls button[data-state="play"] {
  background-image: url("data:image/png;base64,");
}
.vp-controls button[data-state="pause"] {
  background-image: url("data:image/png;base64,");
}
.vp-controls button[data-state="stop"] {
  background-image: url("data:image/png;base64,");
}
.vp-controls button[data-state="mute"] {
  background-image: url("data:image/png;base64,");
}
.vp-controls button[data-state="unmute"] {
  background-image: url("data:image/png;base64,");
}
.vp-controls button[data-state="volup"] {
  background-image: url("data:image/png;base64,");
}
.vp-controls button[data-state="voldown"] {
  background-image: url("data:image/png;base64,");
}
.vp-controls button[data-state="go-fullscreen"] {
  background-image: url("data:image/png;base64,");
}
.vp-controls button[data-state="cancel-fullscreen"] {
  background-image: url("data:image/png;base64,");
}
.vp-controls button[data-state="subtitles"] {
  height: 85%;
  text-indent: 0;
  font-size: 16px;
  font-size: 1rem;
  font-weight: bold;
  color: #666;
  background: #000;
  border-radius: 2px;
}
.vp-controls progress {
  display: block;
  width: 100%;
  height: 81%;
  margin-top: 2px;
  margin-top: 0.125rem;
  border: none;
  overflow: hidden;
  border-radius: 2px;
  color: #0095dd; /* Internet Explorer uses this value as the progress bar's value colour */
}
.vp-controls progress[data-state="fake"] {
  background: #e6e6e6;
  height: 65%;
}
.vp-controls progress span {
  width: 0%;
  height: 100%;
  display: inline-block;
  background-color: #2a84cd;
}
.vp-controls progress::-moz-progress-bar {
  background-color: #0095dd;
}
/* Chrome requires its own rule for this, otherwise it ignores it */
.vp-controls progress::-webkit-progress-value {
  background-color: #0095dd;
}
/* subtitles menu */
/* I added these following lines */
.subtitles {
  position: absolute;
  bottom: 10%;
}
.subRow {
  background-color: rgba(0, 0, 0, 0);
  border: none;
  font-size: xx-large;
  font-weight: bold;
  color: white;
}
.image {
  display: block;
  width: 800px;
}

/* I added these up */

.vp-subtitles-menu {
  display: none;
  position: absolute;
  bottom: 14.8%;
  right: 20px;
  background: #666;
  list-style-type: none;
  margin: 0;
  padding: 0;
  width: 100px;
  padding: 10px;
}
.vp-subtitles-menu li {
  padding: 0;
  text-align: center;
}
.vp-subtitles-menu li button {
  border: none;
  background: #000;
  color: #fff;
  cursor: pointer;
  width: 90%;
  padding: 2px 5px;
  border-radius: 2px;
}

/* WebVTT cues */
::cue {
  color: #ccc;
}
::cue(v[voice="Test"]) {
  color: #fff;
  background: #0095dd;
}

/* fullscreen */
html:-ms-fullscreen {
  width: 100%;
}
:-webkit-full-screen {
  background-color: transparent;
}
video:-webkit-full-screen + .vp-controls {
  background: #ccc; /* required for Chrome which doesn't heed the transparent value set above */
}
video:-webkit-full-screen + .vp-controls progress {
  margin-top: 0.5rem;
}

/* hide controls on fullscreen with WebKit */
figure[data-fullscreen="true"] video::-webkit-media-controls {
  display: none !important;
}
figure[data-fullscreen="true"] {
  max-width: 100%;
  width: 100%;
  margin: 0;
  padding: 0;
  max-height: 100%;
}
figure[data-fullscreen="true"] video {
  height: auto;
}
figure[data-fullscreen="true"] figcaption {
  display: none;
}
figure[data-fullscreen="true"] .vp-controls {
  position: absolute;
  bottom: 2%;
  width: 100%;
  z-index: 2147483647;
}
figure[data-fullscreen="true"] .vp-controls li {
  width: 5%;
}
figure[data-fullscreen="true"] .vp-controls .vp-progress {
  width: 68%;
}

/* Media Queries */
@media screen and (max-width: 1024px) {
  figure {
    padding-left: 0;
    padding-right: 0;
    height: auto;
  }
  .vp-controls {
    /* we want the buttons to be proportionally bigger, so give their parent a set height */
    height: 30px;
    height: 1.876rem;
  }
  .vp-controls button[data-state="subtitles"] {
    font-size: 0.6875rem;
  }
  .vp-subtitles-menu {
    right: 0;
  }
  .vp-subtitles-menu li button {
    font-size: 0.6875rem;
  }
}
@media screen and (max-width: 42.5em) {
  .vp-controls {
    height: auto;
  }
  .vp-controls > * {
    display: block;
    width: 14.5667%;
    margin-left: 0;
    height: 40px;
    height: 2.5rem;
    margin-top: 2.5rem;
  }
  .vp-controls .vp-progress {
    /*display:table-subtitle;*/ /* this trick doesn't work as elements are floated and the layout doesn't work */
    position: absolute;
    top: 0;
    width: 100%;
    float: none;
    margin-top: 0;
  }
  .vp-controls .vp-progress progress {
    width: 98%;
    margin: 0 auto;
  }
  .vp-controls button {
    background-position: center center;
  }
  .vp-controls button[data-state="subtitles"] {
    width: 10%;
    height: 2.2rem;
  }
  .vp-subtitles-menu {
    bottom: 42%;
  }
  figcaption {
    text-align: center;
    margin-top: 0.5rem;
  }
}

.my-modal-background {
  /* z-index: 2147483647; */
  height: 100vh;
  position: absolute;
  bottom: 0;
  width: 100%;
  background-color: rgba(128, 128, 128, 0.562);
}
.my-modal {
  /* z-index: 2147483647; */
  /* position: fixed; */
  /* top: 22vh;
  left: 10%;
  width: 80%; */
  position: absolute;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  background: white;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.26);
  border-radius: 8px;
}

.my-modal__header {
  width: 100%;
  padding: 1rem 0.5rem;
  background: #2a006e;
  color: white;
}

.my-modal__header h2 {
  margin: 0.5rem;
}

.my-modal__content {
  padding: 1rem 0.5rem;
}

.my-modal__footer {
  padding: 1rem 0.5rem;
}

/* @media (min-width: 768px) {
  .my-modal {
    left: calc(50% - 20rem);
    width: 40rem;
  }
} */

.my-modal-enter {
  -webkit-transform: translateY(-10rem);
          transform: translateY(-10rem);
  opacity: 0;
}

.my-modal-enter-active {
  -webkit-transform: translateY(0);
          transform: translateY(0);
  opacity: 1;
  transition: all 200ms;
}

.my-modal-exit {
  -webkit-transform: translateY(0);
          transform: translateY(0);
  opacity: 1;
}

.my-modal-exit-active {
  -webkit-transform: translateY(-10rem);
          transform: translateY(-10rem);
  opacity: 0;
  transition: all 200ms;
}

